import styled, { keyframes } from 'styled-components';

const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

const glow = keyframes`
  0% { box-shadow: 0 0 5px ${props => props.theme.colors.text.accent}33; }
  50% { box-shadow: 0 0 20px ${props => props.theme.colors.text.accent}66; }
  100% { box-shadow: 0 0 5px ${props => props.theme.colors.text.accent}33; }
`;

const SpinnerWrapper = styled.div.attrs({
  role: 'alert',
  'aria-busy': 'true',
  'aria-label': 'Loading content'
})`
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
`;

const Spinner = styled.div.attrs({
  'aria-hidden': 'true'
})`
  width: 48px;
  height: 48px;
  border: 3px solid ${props => props.theme.colors.glass.border};
  border-top-color: ${props => props.theme.colors.text.accent};
  border-radius: 50%;
  animation: ${spin} 1s linear infinite, ${glow} 2s ease-in-out infinite;
`;

export const LoadingSpinner = () => (
  <SpinnerWrapper>
    <Spinner />
  </SpinnerWrapper>
);