const LinkedInGrowth = () => {
  const [completedTasks, setCompletedTasks] = useState(new Set());
  const [streak, setStreak] = useState(7);

  const toggleTask = (taskId) => {
    const newCompleted = new Set(completedTasks);
    if (newCompleted.has(taskId)) {
      newCompleted.delete(taskId);
    } else {
      newCompleted.add(taskId);
    }
    setCompletedTasks(newCompleted);
  };

  const completionPercentage = (completedTasks.size / dailyTasks.length) * 100;

  return (
    <SectionWrapper>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           15-Minute Daily LinkedIn Plan
        </Title>
        <Subtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          Strategic networking approach that delivers 50% visibility boost in just 15 minutes daily
        </Subtitle>
      </Header>

      <PlanOverview>
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          style={{ 
            textAlign: 'center', 
            marginBottom: '2rem',
            color: '#e0e0e0',
            fontSize: '2rem'
          }}
        >
           Your Daily Action Plan
        </motion.h2>

        <TimeAllocation>
          {dailyActivities.map((activity, index) => (
            <ActivityCard
              key={index}
              color={activity.color}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              whileHover={{ scale: 1.05 }}
            >
              <div className="icon">{activity.icon}</div>
              <div className="time">{activity.time}</div>
              <div className="activity">{activity.activity}</div>
              <div className="description">{activity.description}</div>
            </ActivityCard>
          ))}
        </TimeAllocation>
      </PlanOverview>

      <InteractiveTracker>
        <TrackerHeader>
          <div className="title"> Today's Tasks</div>
          <div className="streak">
            <span className="flame"></span>
            <span className="number">{streak}</span>
            <span className="label">day streak</span>
          </div>
        </TrackerHeader>

        <TasksList>
          {dailyTasks.map((task, index) => (
            <TaskItem
              key={task.id}
              color={task.color}
              completed={completedTasks.has(task.id)}
              onClick={() => toggleTask(task.id)}
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: index * 0.1 }}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
            >
              <div className="checkbox">
                <span className="checkmark"></span>
              </div>
              <div className="content">
                <div className="task-title">{task.title}</div>
                <div className="task-description">{task.description}</div>
              </div>
              <div className="time-badge">{task.time}</div>
            </TaskItem>
          ))}
        </TasksList>

        <motion.div
          style={{
            marginTop: '1.5rem',
            padding: '1rem',
            background: 'rgba(0, 255, 231, 0.1)',
            borderRadius: '12px',
            textAlign: 'center'
          }}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.5 }}
        >
          <div style={{ color: '#00ffe7', fontSize: '1.2rem', fontWeight: 'bold' }}>
            {Math.round(completionPercentage)}% Complete
          </div>
          <div style={{ color: '#a0a0a0', fontSize: '0.9rem', marginTop: '0.5rem' }}>
            {completedTasks.size} of {dailyTasks.length} tasks completed
          </div>
        </motion.div>
      </InteractiveTracker>

      <ProgressSection>
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          style={{ 
            textAlign: 'center', 
            marginBottom: '2rem',
            color: '#e0e0e0',
            fontSize: '2rem'
          }}
        >
           Expected Results
        </motion.h2>

        <ProgressGrid>
          {progressMetrics.map((progress, index) => (
            <ProgressCard
              key={index}
              color={progress.color}
            >
              <motion.div
                className="metric"
                initial={{ opacity: 0, scale: 0.5 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                transition={{ delay: index * 0.1 }}
              >
                {progress.metric}
              </motion.div>
              <div className="label">{progress.label}</div>
              <div className="change">{progress.change}</div>
            </ProgressCard>
          ))}
        </ProgressGrid>
      </ProgressSection>

      <TipsSection>
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          style={{ 
            textAlign: 'center', 
            marginBottom: '2rem',
            color: '#e0e0e0',
            fontSize: '2rem'
          }}
        >
           Pro Tips for Maximum Impact
        </motion.h2>

        {growthTips.map((tip, index) => (
          <TipCard
            key={index}
            color={tip.color}
            initial={{ opacity: 0, x: -20 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ delay: index * 0.1 }}
          >
            <div className="tip-header">
              <span className="icon">{tip.icon}</span>
              <span className="title">{tip.title}</span>
            </div>
            <div className="tip-content">{tip.content}</div>
          </TipCard>
        ))}
      </TipsSection>
    </SectionWrapper>
  );
};

