const ColdOutreach = () => {
  const [selectedTemplate, setSelectedTemplate] = useState(0);
  const [demoInputs, setDemoInputs] = useState({
    name: "",
    company: "",
    role: "",
    university: "",
    skill: "",
    experience: "",
    accomplishment: ""
  });
  const [generatedMessage, setGeneratedMessage] = useState("");

  // Generate message when inputs change
  useEffect(() => {
    const template = templateData[selectedTemplate];
    let message = template.template;
    
    // Replace variables with user inputs
    Object.entries(demoInputs).forEach(([key, value]) => {
      const placeholder = `[${key === 'name' ? 'Name' : 
                           key === 'company' ? 'Company' : 
                           key === 'role' ? 'Role' : 
                           key === 'university' ? 'University/Bootcamp' : 
                           key === 'skill' ? 'key skill' : 
                           key === 'experience' ? 'X years experience or key project' : 
                           key === 'accomplishment' ? 'brief relevant accomplishment' : key}]`;
      message = message.replace(new RegExp(placeholder, 'g'), value || placeholder);
    });
    
    setGeneratedMessage(message);
  }, [demoInputs, selectedTemplate]);

  const copyToClipboard = () => {
    navigator.clipboard.writeText(generatedMessage);
  };

  return (
    <SectionWrapper>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           LinkedIn Cold Outreach Mastery
        </Title>
        <Subtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          Proven strategies and templates for effective LinkedIn messaging that get responses
        </Subtitle>
      </Header>

      <StatsGrid>
        {statsData.map((stat, index) => (
          <StatCard
            key={index}
            color={stat.color}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            whileHover={{ scale: 1.05 }}
          >
            <StatNumber color={stat.color}>{stat.number}</StatNumber>
            <StatLabel>{stat.label}</StatLabel>
            <StatDescription>{stat.description}</StatDescription>
          </StatCard>
        ))}
      </StatsGrid>

      <TemplateSection>
        <SectionTitle> High-Converting Message Templates</SectionTitle>
        <TemplateGrid>
          {templateData.map((template, index) => (
            <TemplateCard
              key={index}
              color={template.color}
              initial={{ opacity: 0, x: index % 2 === 0 ? -50 : 50 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ delay: index * 0.1 }}
            >
              <TemplateHeader>
                <TemplateType color={template.color}>
                  {template.type}
                </TemplateType>
                <SuccessRate>{template.successRate} Success Rate</SuccessRate>
              </TemplateHeader>
              
              <TemplateContent color={template.color}>
                {template.template.split(/(\[.*?\])/).map((part, idx) => 
                  part.startsWith('[') && part.endsWith(']') ? (
                    <span key={idx} className="variable">{part}</span>
                  ) : (
                    part
                  )
                )}
              </TemplateContent>
              
              <TemplateTips color={template.color}>
                {template.tips.map((tip, tipIndex) => (
                  <div key={tipIndex} className="tip">
                    <span className="icon"></span>
                    <span className="text">{tip}</span>
                  </div>
                ))}
              </TemplateTips>
            </TemplateCard>
          ))}
        </TemplateGrid>
      </TemplateSection>

      <InteractiveDemo>
        <SectionTitle> Interactive Message Generator</SectionTitle>
        
        <DemoInputs>
          <InputGroup>
            <label className="label">Template Type</label>
            <select 
              value={selectedTemplate} 
              onChange={(e) => setSelectedTemplate(Number(e.target.value))}
            >
              {templateData.map((template, index) => (
                <option key={index} value={index}>{template.type}</option>
              ))}
            </select>
          </InputGroup>
          
          <InputGroup>
            <label className="label">Recipient Name</label>
            <input 
              type="text" 
              placeholder="Sarah Johnson"
              value={demoInputs.name}
              onChange={(e) => setDemoInputs(prev => ({...prev, name: e.target.value}))}
            />
          </InputGroup>
          
          <InputGroup>
            <label className="label">Target Company</label>
            <input 
              type="text" 
              placeholder="Google"
              value={demoInputs.company}
              onChange={(e) => setDemoInputs(prev => ({...prev, company: e.target.value}))}
            />
          </InputGroup>
          
          <InputGroup>
            <label className="label">Target Role</label>
            <input 
              type="text" 
              placeholder="Senior ML Engineer"
              value={demoInputs.role}
              onChange={(e) => setDemoInputs(prev => ({...prev, role: e.target.value}))}
            />
          </InputGroup>
          
          <InputGroup>
            <label className="label">Shared Background</label>
            <input 
              type="text" 
              placeholder="Stanford University"
              value={demoInputs.university}
              onChange={(e) => setDemoInputs(prev => ({...prev, university: e.target.value}))}
            />
          </InputGroup>
          
          <InputGroup>
            <label className="label">Key Skill/Tech</label>
            <input 
              type="text" 
              placeholder="PyTorch, NLP"
              value={demoInputs.skill}
              onChange={(e) => setDemoInputs(prev => ({...prev, skill: e.target.value}))}
            />
          </InputGroup>
        </DemoInputs>

        <GeneratedMessage>
          <button className="copy-button" onClick={copyToClipboard}>
             Copy
          </button>
          {generatedMessage}
        </GeneratedMessage>
      </InteractiveDemo>
    </SectionWrapper>
  );
};

export default ColdOutreach;