import React, { createContext, useContext, useReducer, useEffect, useCallback } from 'react';
import { sessionManager, backupNotes, STORAGE_KEYS } from '../utils/sessionManager';
import { API_ENDPOINTS, handleApiResponse } from '../config/api';

// Password validation constants
const PASSWORD_MIN_LENGTH = 8;
const PASSWORD_REGEX = {
  uppercase: /[A-Z]/,
  lowercase: /[a-z]/,
  number: /[0-9]/,
  special: /[!@#$%^&*(),.?":{}|<>]/
};

// Initial user state
const initialUserState = {
  id: null,
  email: '',
  name: '',
  gender: '',
  createdAt: null,
  notes: {},
  preferences: {},
  isAuthenticated: false,
  hasProfile: false,
  isContentUnlocked: false,
  chatHistory: [],
  lastActivity: null,
};

// Action types
const USER_ACTIONS = {
  SET_USER: 'SET_USER',
  UPDATE_PROFILE: 'UPDATE_PROFILE',
  ADD_NOTE: 'ADD_NOTE',
  UPDATE_NOTE: 'UPDATE_NOTE',
  DELETE_NOTE: 'DELETE_NOTE',
  SET_CONTENT_UNLOCKED: 'SET_CONTENT_UNLOCKED',
  ADD_CHAT_MESSAGE: 'ADD_CHAT_MESSAGE',
  UPDATE_PREFERENCES: 'UPDATE_PREFERENCES',
  RESET_USER: 'RESET_USER',
  SET_AUTH_STATE: 'SET_AUTH_STATE'
};

// User reducer
const userReducer = (state, action) => {
  switch (action.type) {
    case USER_ACTIONS.SET_USER:
      return {
        ...state,
        ...action.payload,
        isAuthenticated: true
      };
    
    case USER_ACTIONS.UPDATE_PROFILE:
      return {
        ...state,
        ...action.payload
      };
    
    case USER_ACTIONS.ADD_NOTE:
      const { sectionId, note } = action.payload;
      return {
        ...state,
        notes: {
          ...state.notes,
          [sectionId]: [
            ...(state.notes[sectionId] || []),
            {
              id: `note-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
              content: note.content,
              createdAt: new Date().toISOString(),
              updatedAt: new Date().toISOString()
            }
          ]
        }
      };
    
    case USER_ACTIONS.UPDATE_NOTE:
      const { sectionId: updateSectionId, noteId, content } = action.payload;
      return {
        ...state,
        notes: {
          ...state.notes,
          [updateSectionId]: state.notes[updateSectionId]?.map(note =>
            note.id === noteId
              ? { ...note, content, updatedAt: new Date().toISOString() }
              : note
          ) || []
        }
      };
    
    case USER_ACTIONS.DELETE_NOTE:
      const { sectionId: deleteSectionId, noteId: deleteNoteId } = action.payload;
      return {
        ...state,
        notes: {
          ...state.notes,
          [deleteSectionId]: state.notes[deleteSectionId]?.filter(note =>
            note.id !== deleteNoteId
          ) || []
        }
      };
    
    case USER_ACTIONS.SET_CONTENT_UNLOCKED:
      return {
        ...state,
        isContentUnlocked: action.payload
      };
    
    case USER_ACTIONS.ADD_CHAT_MESSAGE:
      return {
        ...state,
        chatHistory: [
          ...state.chatHistory,
          {
            id: `msg-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
            ...action.payload,
            timestamp: new Date().toISOString()
          }
        ]
      };
    
    case USER_ACTIONS.UPDATE_PREFERENCES:
      return {
        ...state,
        preferences: {
          ...state.preferences,
          ...action.payload
        }
      };
    
    case USER_ACTIONS.RESET_USER:
      return {
        ...initialUserState,
        notes: action.payload?.preservedNotes || {}
      };
    
    case USER_ACTIONS.SET_AUTH_STATE:
      return {
        ...state,
        ...action.payload
      };
    
    default:
      return state;
  }
};

// Create context
const UserContext = createContext();

// User provider component
export const UserProvider = ({ children }) => {
  const [user, dispatch] = useReducer(userReducer, initialUserState);
  
  // Load notes from database
  const loadNotes = useCallback(async () => {
    try {
      console.log(' loadNotes called, checking auth state...');
      console.log('isAuthenticated:', sessionManager.isAuthenticated());
      console.log('has token:', !!sessionManager.token);
      
      if (!sessionManager.isAuthenticated() || !sessionManager.token) {
        console.log(' No authentication or token available');
        return;
      }

      console.log(' Making API call to load notes...');
      const response = await fetch(API_ENDPOINTS.notes.getAll, {
        headers: {
          'Authorization': `Bearer ${sessionManager.token}`
        }
      });

      if (response.ok) {
        const result = await response.json();
        console.log(' Notes loaded from database:', result.notes);
        // Update user state with loaded notes
        dispatch({
          type: USER_ACTIONS.UPDATE_PROFILE,
          payload: {
            notes: result.notes || {}
          }
        });
        console.log(' Notes updated in user state');
        return result.notes;
      } else {
        console.error('Failed to load notes from database, status:', response.status);
        return {};
      }
    } catch (error) {
      console.error('Error loading notes:', error);
      return {};
    }
  }, []);
  
  // Initialize sessionManager and verify session on mount
  useEffect(() => {
    const initializeUser = async () => {
      try {
        // Initialize session manager
        sessionManager.init();
        
        // Check if user is already authenticated from persisted storage
        if (sessionManager.isAuthenticated()) {
          const currentUser = sessionManager.getCurrentUser();
          if (currentUser) {
            // Set user from stored data immediately
            dispatch({
              type: USER_ACTIONS.SET_USER,
              payload: currentUser
            });
          }
          
          // Verify session with server to ensure token is still valid
          const verifiedUser = await sessionManager.verifySession();
          if (verifiedUser) {
            dispatch({
              type: USER_ACTIONS.SET_USER,
              payload: verifiedUser
            });
            // Load notes from database after successful verification
            await loadNotes();
          } else {
            // Session invalid, clear stored data
            await sessionManager.logout();
            dispatch({ type: USER_ACTIONS.RESET_USER });
          }
        }
      } catch (error) {
        console.error('Failed to initialize user session:', error);
        // Clear any invalid session data
        await sessionManager.logout();
        dispatch({ type: USER_ACTIONS.RESET_USER });
      }
    };

    if (typeof window !== 'undefined') {
      initializeUser();
    }
  }, [loadNotes]);

  // Load notes when user becomes authenticated
  useEffect(() => {
    if (user.isAuthenticated && sessionManager.token) {
      console.log(' User authentication state changed, loading notes...');
      loadNotes();
    }
  }, [user.isAuthenticated, loadNotes]);

  // Save user data to localStorage whenever user state changes
  useEffect(() => {
    try {
      if (user.notes && Object.keys(user.notes).length > 0) {
        backupNotes(user.notes);
      }
    } catch (error) {
      console.error('Error backing up notes:', error);
    }
  }, [user.notes]);

  // Action creators
  const updateProfile = async (updates) => {
    try {
      // Import CSRF service dynamically to avoid circular dependencies
      const { default: csrfService } = await import('../services/csrfService.js');
      
      // Get CSRF headers
      const headers = await csrfService.getCSRFHeaders(sessionManager.token);
      
      const response = await fetch(API_ENDPOINTS.auth.updateProfile, {
        method: 'PUT',
        headers: headers,
        body: JSON.stringify(updates)
      });

      const updatedUser = await handleApiResponse(response);
      
      dispatch({
        type: USER_ACTIONS.UPDATE_PROFILE,
        payload: updatedUser
      });

      return updatedUser;
    } catch (error) {
      console.error('Failed to update profile:', error);
      throw error;
    }
  };

  const addNote = async (sectionId, noteContent) => {
    try {
      // Optimistically update local state first
      dispatch({
        type: USER_ACTIONS.ADD_NOTE,
        payload: {
          sectionId,
          note: {
            content: noteContent,
            timestamp: new Date().toISOString()
          }
        }
      });

      // If user is authenticated, also save to database
      if (sessionManager.isAuthenticated() && sessionManager.token) {
        // Import CSRF service dynamically
        const { default: csrfService } = await import('../services/csrfService.js');
        
        // Get CSRF headers
        const headers = await csrfService.getCSRFHeaders(sessionManager.token);
        
        const response = await fetch(API_ENDPOINTS.notes.create, {
          method: 'POST',
          headers: headers,
          body: JSON.stringify({
            sectionId,
            content: noteContent
          })
        });

        if (!response.ok) {
          console.error('Failed to save note to database');
          // Note: We don't revert the local state to provide offline-like experience
        } else {
          const result = await response.json();
          console.log('Note saved to database:', result.note.id);
        }
      }
    } catch (error) {
      console.error('Error adding note:', error);
      // Note continues to work locally even if API fails
    }
  };

  const updateNote = async (sectionId, noteId, content) => {
    try {
      // Optimistically update local state first
      dispatch({
        type: USER_ACTIONS.UPDATE_NOTE,
        payload: { sectionId, noteId, content }
      });

      // If user is authenticated, also update in database
      if (sessionManager.isAuthenticated() && sessionManager.token) {
        // Import CSRF service dynamically
        const { default: csrfService } = await import('../services/csrfService.js');
        
        // Get CSRF headers
        const headers = await csrfService.getCSRFHeaders(sessionManager.token);
        
        const response = await fetch(API_ENDPOINTS.notes.update(sectionId, noteId), {
          method: 'PUT',
          headers: headers,
          body: JSON.stringify({
            content
          })
        });

        if (!response.ok) {
          console.error('Failed to update note in database');
        } else {
          const result = await response.json();
          console.log('Note updated in database:', result.note.id);
        }
      }
    } catch (error) {
      console.error('Error updating note:', error);
    }
  };

  const deleteNote = async (sectionId, noteId) => {
    try {
      // Optimistically update local state first
      dispatch({
        type: USER_ACTIONS.DELETE_NOTE,
        payload: { sectionId, noteId }
      });

      // If user is authenticated, also delete from database
      if (sessionManager.isAuthenticated() && sessionManager.token) {
        // Import CSRF service dynamically
        const { default: csrfService } = await import('../services/csrfService.js');
        
        // Get CSRF headers
        const headers = await csrfService.getCSRFHeaders(sessionManager.token);
        
        const response = await fetch(API_ENDPOINTS.notes.delete(sectionId, noteId), {
          method: 'DELETE',
          headers: headers
        });

        if (!response.ok) {
          console.error('Failed to delete note from database');
        } else {
          const result = await response.json();
          console.log('Note deleted from database:', result.noteId);
        }
      }
    } catch (error) {
      console.error('Error deleting note:', error);
    }
  };

  // Sync local notes with database
  const syncNotes = async () => {
    try {
      if (!sessionManager.isAuthenticated() || !sessionManager.token) {
        return;
      }

      const { default: csrfService } = await import('../services/csrfService.js');
      const headers = await csrfService.getCSRFHeaders(sessionManager.token);

      const response = await fetch(API_ENDPOINTS.notes.sync, {
        method: 'POST',
        headers: {
          ...headers,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          notes: user.notes
        })
      });

      if (response.ok) {
        const result = await response.json();
        dispatch({
          type: USER_ACTIONS.UPDATE_PROFILE,
          payload: {
            notes: result.notes || {}
          }
        });
        console.log('Notes synced successfully');
        return result.notes;
      } else {
        console.error('Failed to sync notes with database');
        return user.notes;
      }
    } catch (error) {
      console.error('Error syncing notes:', error);
      return user.notes;
    }
  };

  const setContentUnlocked = (unlocked) => {
    dispatch({
      type: USER_ACTIONS.SET_CONTENT_UNLOCKED,
      payload: unlocked
    });
  };

  const addChatMessage = (message) => {
    dispatch({
      type: USER_ACTIONS.ADD_CHAT_MESSAGE,
      payload: message
    });
  };

  const updatePreferences = (preferences) => {
    dispatch({
      type: USER_ACTIONS.UPDATE_PREFERENCES,
      payload: preferences
    });
  };

  const resetUser = () => {
    const savedNotes = user.notes;
    dispatch({
      type: USER_ACTIONS.RESET_USER,
      payload: { preservedNotes: savedNotes }
    });
  };

  const validatePassword = (password) => {
    if (!password || password.length < PASSWORD_MIN_LENGTH) {
      return { isValid: false, message: `Password must be at least ${PASSWORD_MIN_LENGTH} characters long` };
    }

    const requirements = [
      { check: PASSWORD_REGEX.uppercase.test(password), message: 'Must contain uppercase letter' },
      { check: PASSWORD_REGEX.lowercase.test(password), message: 'Must contain lowercase letter' },
      { check: PASSWORD_REGEX.number.test(password), message: 'Must contain number' },
      { check: PASSWORD_REGEX.special.test(password), message: 'Must contain special character' }
    ];

    const failedRequirement = requirements.find(req => !req.check);
    if (failedRequirement) {
      return { isValid: false, message: failedRequirement.message };
    }

    return { isValid: true };
  };

  const setUser = (userData) => {
    dispatch({
      type: USER_ACTIONS.SET_USER,
      payload: userData
    });
  };

  const getSectionNotes = (sectionId) => {
    return user.notes[sectionId] || [];
  };
  
  // Add authenticateUser function
  const authenticateUser = (userData) => {
    if (!userData) return;
    
    dispatch({
      type: USER_ACTIONS.SET_USER,
      payload: userData
    });
  };
  
  // Add createUserProfile function
  const createUserProfile = (profileData) => {
    if (!profileData) return null;
    
    const sessionId = `session-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    
    // Update user with profile data
    dispatch({
      type: USER_ACTIONS.UPDATE_PROFILE,
      payload: {
        ...profileData,
        sessionId,
        hasProfile: true,
        createdAt: new Date().toISOString()
      }
    });
    
    // Save profile to backend if we have a token
    if (sessionManager.token) {
      updateProfile(profileData).catch(err => {
        console.error('Failed to save profile to backend:', err);
      });
    }
    
    return sessionId;
  };

  // Add login function
  const login = async (credentials) => {
    try {
      console.log(' UserContext login called with:', credentials.email);
      const result = await sessionManager.login(credentials);
      console.log(' SessionManager login result:', result);
      
      if (result.success && result.user) {
        dispatch({
          type: USER_ACTIONS.SET_USER,
          payload: result.user
        });
        
        console.log(' Waiting before loading notes after login...');
        // Wait a moment for the state to update, then load notes
        setTimeout(async () => {
          console.log(' Loading notes after login...');
          await loadNotes();
        }, 500);
        
        return result;
      }
      throw new Error('Login failed');
    } catch (error) {
      console.error('Login error in UserContext:', error);
      throw error;
    }
  };

  // Add signup function
  const signup = async (userData) => {
    try {
      console.log(' UserContext signup called with:', userData.email);
      const result = await sessionManager.signup(userData);
      console.log(' SessionManager signup result:', result);
      
      if (result.success && result.user) {
        dispatch({
          type: USER_ACTIONS.SET_USER,
          payload: result.user
        });
        
        console.log(' Waiting before loading notes after signup...');
        // Wait a moment for the state to update, then load notes
        setTimeout(async () => {
          console.log(' Loading notes after signup...');
          await loadNotes();
        }, 500);
        
        return result;
      }
      throw new Error('Signup failed');
    } catch (error) {
      console.error('Signup error in UserContext:', error);
      throw error;
    }
  };

  // Add logout function
  const logout = async () => {
    try {
      await sessionManager.logout();
      dispatch({ type: USER_ACTIONS.RESET_USER });
    } catch (error) {
      console.error('Logout error in UserContext:', error);
      // Clear local state even if server logout fails
      dispatch({ type: USER_ACTIONS.RESET_USER });
    }
  };

  const value = {
    user,
    setUser,
    login,
    signup,
    logout,
    updateProfile,
    addNote,
    updateNote,
    deleteNote,
    loadNotes,
    syncNotes,
    setContentUnlocked,
    addChatMessage,
    updatePreferences,
    resetUser,
    getSectionNotes,
    validatePassword,
    authenticateUser,
    createUserProfile,
    isAuthenticated: user.isAuthenticated
  };

  return (
    <UserContext.Provider value={value}>
      {children}
    </UserContext.Provider>
  );
};

// Custom hook to use user context
export const useUser = () => {
  const context = useContext(UserContext);
  if (!context) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
};

export default UserContext;