// Determine if we're in production based on hostname
const isProduction = typeof window !== 'undefined' && 
  (window.location.hostname === 'leochlon.github.io' || 
   window.location.hostname.includes('github.io'));

// Use the Fly.io backend URL in production, otherwise use localhost
const API_URL = isProduction 
  ? 'https://ai-careers-backend.fly.dev'
  : (process.env.REACT_APP_API_URL || 'http://localhost:5002');

console.log('Using API URL:', API_URL);

export const API_ENDPOINTS = {
  auth: {
    login: `${API_URL}/api/auth/login`,
    signup: `${API_URL}/api/auth/signup`,
    logout: `${API_URL}/api/auth/logout`,
    refresh: `${API_URL}/api/auth/refresh-token`,
    verify: `${API_URL}/api/auth/me`,
    updateProfile: `${API_URL}/api/auth/profile`,
  },
  notes: {
    base: `${API_URL}/api/notes`,
    getAll: `${API_URL}/api/notes`,
    getBySection: (sectionId) => `${API_URL}/api/notes/${sectionId}`,
    create: `${API_URL}/api/notes`,
    update: (sectionId, noteId) => `${API_URL}/api/notes/${sectionId}/${noteId}`,
    delete: (sectionId, noteId) => `${API_URL}/api/notes/${sectionId}/${noteId}`,
    sync: `${API_URL}/api/notes/sync`,
  }
};

export const getAuthHeaders = (token) => ({
  'Content-Type': 'application/json',
  'Authorization': token ? `Bearer ${token}` : undefined,
});

export const handleApiResponse = async (response) => {
  if (!response.ok) {
    const error = await response.json().catch(() => ({
      message: 'An error occurred while processing your request'
    }));
    throw new Error(error.message || 'Network response was not ok');
  }
  return response.json();
};