// Secure session management utilities for AI Careers application

// Import API endpoints
import { API_ENDPOINTS } from '../config/api';

// Secure token handling
const CSRF_TOKEN_HEADER = 'X-CSRF-Token';
const SESSION_COOKIE_NAME = 'ai_careers_session';

/**
 * Generates cryptographically secure random token
 * @returns {string} Secure random token
 */
const generateSecureToken = () => {
  const array = new Uint8Array(32);
  crypto.getRandomValues(array);
  return Array.from(array, byte => byte.toString(16).padStart(2, '0')).join('');
};

/**
 * Generates a secure session ID
 * @returns {string} Secure session ID
 */
const generateSessionId = () => {
  return generateSecureToken();
};

/**
 * Handles API response and errors
 * @param {Response} response - Fetch API response
 * @returns {Promise<Object>} Parsed response data
 * @throws {Error} API error with message
 */
const handleApiResponse = async (response) => {
  const data = await response.json().catch(() => ({}));
  
  if (!response.ok) {
    const errorMsg = data.error || data.message || `Request failed: ${response.statusText}`;
    throw new Error(errorMsg);
  }
  
  return data;
};

/**
 * Storage keys for non-sensitive data
 */
export const STORAGE_KEYS = {
  NOTES_BACKUP: 'aiCareersNotesBackup',
  AUTH_TOKEN: 'aiCareersAuthToken',
  REFRESH_TOKEN: 'aiCareersRefreshToken',
  USER_DATA: 'aiCareersUserData'
};

/**
 * Validates user profile data structure
 * @param {Object} profileData - Profile data to validate
 * @returns {boolean} True if valid, false otherwise
 */
export const validateProfileData = (profileData) => {
  if (!profileData || typeof profileData !== 'object') {
    return false;
  }

  const requiredFields = ['sessionId', 'name', 'gender', 'createdAt'];
  return requiredFields.every(field => 
    profileData.hasOwnProperty(field) && profileData[field] !== null
  );
};

/**
 * Safely saves data to localStorage with error handling
 * @param {string} key - Storage key
 * @param {any} data - Data to store
 * @returns {boolean} True if successful, false otherwise
 */
export const safeLocalStorageSet = (key, data) => {
  try {
    const serializedData = JSON.stringify(data);
    localStorage.setItem(key, serializedData);
    return true;
  } catch (error) {
    console.error(`Error saving to localStorage (${key}):`, error);
    return false;
  }
};

/**
 * Safely retrieves data from localStorage with error handling
 * @param {string} key - Storage key
 * @param {any} defaultValue - Default value if retrieval fails
 * @returns {any} Retrieved data or default value
 */
export const safeLocalStorageGet = (key, defaultValue = null) => {
  try {
    const item = localStorage.getItem(key);
    return item ? JSON.parse(item) : defaultValue;
  } catch (error) {
    console.error(`Error reading from localStorage (${key}):`, error);
    return defaultValue;
  }
};

/**
 * Removes data from localStorage safely
 * @param {string} key - Storage key to remove
 * @returns {boolean} True if successful, false otherwise
 */
export const safeLocalStorageRemove = (key) => {
  try {
    localStorage.removeItem(key);
    return true;
  } catch (error) {
    console.error(`Error removing from localStorage (${key}):`, error);
    return false;
  }
};

/**
 * Creates a backup of user notes
 * @param {Object} notes - Notes object to backup
 * @returns {boolean} True if backup successful
 */
export const backupNotes = (notes) => {
  const backup = {
    timestamp: new Date().toISOString(),
    notes: notes,
    version: '1.0'
  };
  
  return safeLocalStorageSet(STORAGE_KEYS.NOTES_BACKUP, backup);
};

/**
 * Restores notes from backup
 * @returns {Object|null} Restored notes or null if no backup exists
 */
export const restoreNotesFromBackup = () => {
  const backup = safeLocalStorageGet(STORAGE_KEYS.NOTES_BACKUP);
  if (backup && backup.notes) {
    return backup.notes;
  }
  return null;
};

/**
 * Exports user data for download/backup
 * @param {Object} userData - Complete user data
 * @returns {string} JSON string of user data
 */
export const exportUserData = (userData) => {
  const exportData = {
    exportedAt: new Date().toISOString(),
    version: '1.0',
    userData: userData
  };
  
  return JSON.stringify(exportData, null, 2);
};

/**
 * Validates and imports user data
 * @param {string} jsonString - JSON string of user data
 * @returns {Object|null} Validated user data or null if invalid
 */
export const importUserData = (jsonString) => {
  try {
    const importData = JSON.parse(jsonString);
    
    if (!importData.userData || !validateProfileData(importData.userData)) {
      throw new Error('Invalid user data format');
    }
    
    return importData.userData;
  } catch (error) {
    console.error('Error importing user data:', error);
    return null;
  }
};

/**
 * Checks if the current session is still valid
 * @param {Object} userData - User data to check
 * @param {number} maxAge - Maximum age in milliseconds (default: 30 days)
 * @returns {boolean} True if session is valid
 */
export const isSessionValid = (userData, maxAge = 30 * 24 * 60 * 60 * 1000) => {
  if (!userData || !userData.createdAt) {
    return false;
  }
  
  const sessionAge = Date.now() - new Date(userData.createdAt).getTime();
  return sessionAge < maxAge;
};

/**
 * Migrates old data format to new format if needed
 * @param {Object} userData - User data to migrate
 * @returns {Object} Migrated user data
 */
export const migrateUserData = (userData) => {
  if (!userData) return userData;
  
  // Add any migration logic here for future schema changes
  // For now, just ensure all required fields exist
  const migrated = {
    sessionId: userData.sessionId || generateSessionId(),
    name: userData.name || '',
    gender: userData.gender || '',
    createdAt: userData.createdAt || new Date().toISOString(),
    isContentUnlocked: userData.isContentUnlocked || false,
    notes: userData.notes || {},
    chatHistory: userData.chatHistory || [],
    preferences: userData.preferences || {},
    hasProfile: userData.hasProfile !== undefined ? userData.hasProfile : !!(userData.sessionId && userData.name)
  };
  
  return migrated;
};

/**
 * Clears all user-related data from localStorage
 * @returns {boolean} True if successful
 */
export const clearAllUserData = () => {
  try {
    Object.values(STORAGE_KEYS).forEach(key => {
      localStorage.removeItem(key);
    });
    return true;
  } catch (error) {
    console.error('Error clearing user data:', error);
    return false;
  }
};

/**
 * Gets storage usage information
 * @returns {Object} Storage usage stats
 */
export const getStorageUsage = () => {
  try {
    let totalSize = 0;
    const breakdown = {};
    
    Object.entries(STORAGE_KEYS).forEach(([name, key]) => {
      const item = localStorage.getItem(key);
      const size = item ? new Blob([item]).size : 0;
      breakdown[name] = size;
      totalSize += size;
    });
    
    return {
      totalSize,
      breakdown,
      quota: navigator.storage && navigator.storage.estimate ? 
        navigator.storage.estimate() : null
    };
  } catch (error) {
    console.error('Error calculating storage usage:', error);
    return { totalSize: 0, breakdown: {}, quota: null };
  }
};

/**
 * Session manager class for secure session handling
 */
export class SessionManager {
  constructor() {
    this.listeners = new Set();
    this.token = null;
    this.refreshToken = null;
    this.user = null;
    this.loadTokensFromStorage();
  }

  /**
   * Load tokens from localStorage
   */
  loadTokensFromStorage() {
    try {
      this.token = safeLocalStorageGet(STORAGE_KEYS.AUTH_TOKEN);
      this.refreshToken = safeLocalStorageGet(STORAGE_KEYS.REFRESH_TOKEN);
      this.user = safeLocalStorageGet(STORAGE_KEYS.USER_DATA);
    } catch (error) {
      console.error('Error loading tokens from storage:', error);
    }
  }

  /**
   * Save tokens to localStorage
   */
  saveTokensToStorage() {
    try {
      if (this.token) {
        safeLocalStorageSet(STORAGE_KEYS.AUTH_TOKEN, this.token);
      }
      if (this.refreshToken) {
        safeLocalStorageSet(STORAGE_KEYS.REFRESH_TOKEN, this.refreshToken);
      }
      if (this.user) {
        safeLocalStorageSet(STORAGE_KEYS.USER_DATA, this.user);
      }
    } catch (error) {
      console.error('Error saving tokens to storage:', error);
    }
  }

  /**
   * Clear tokens from localStorage
   */
  clearTokensFromStorage() {
    try {
      safeLocalStorageRemove(STORAGE_KEYS.AUTH_TOKEN);
      safeLocalStorageRemove(STORAGE_KEYS.REFRESH_TOKEN);
      safeLocalStorageRemove(STORAGE_KEYS.USER_DATA);
    } catch (error) {
      console.error('Error clearing tokens from storage:', error);
    }
  }

  /**
   * Sets CSRF token for secure requests
   * @param {string} token - CSRF token
   */
  setCsrfToken(token) {
    this.csrfToken = token;
  }

  /**
   * Gets current CSRF token
   * @returns {string} Current CSRF token
   */
  getCsrfToken() {
    return this.csrfToken;
  }

  /**
   * Makes authenticated API request with CSRF protection
   * @param {string} endpoint - API endpoint
   * @param {Object} options - Request options
   * @returns {Promise} API response
   */
  async fetchWithAuth(endpoint, options = {}) {
    // Load tokens from storage if not in memory
    if (!this.token) {
      this.loadTokensFromStorage();
    }
    
    const headers = {
      'Content-Type': 'application/json',
      ...(this.token && { 'Authorization': `Bearer ${this.token}` }),
      ...options.headers
    };

    let response = await fetch(endpoint, {
      ...options,
      headers
    });

    // Handle token refresh if needed
    if (response.status === 401 && this.refreshToken) {
      try {
        const refreshResponse = await fetch(API_ENDPOINTS.auth.refresh, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ refreshToken: this.refreshToken })
        });

        if (refreshResponse.ok) {
          const { token, refreshToken } = await refreshResponse.json();
          this.token = token;
          if (refreshToken) {
            this.refreshToken = refreshToken;
          }
          
          // Persist new tokens
          this.saveTokensToStorage();
          
          // Retry original request with new token
          headers.Authorization = `Bearer ${token}`;
          response = await fetch(endpoint, {
            ...options,
            headers
          });
        }
      } catch (error) {
        console.error('Token refresh failed:', error);
        this.logout();
        throw new Error('Session expired');
      }
    }

    if (!response.ok) {
      const error = await response.json().catch(() => ({
        message: 'An error occurred'
      }));
      throw new Error(error.message || `Request failed: ${response.statusText}`);
    }

    return response.json();
  }

  /**
   * Attempts to login user
   * @param {Object} credentials - User credentials
   * @returns {Promise<Object>} Login result
   */
  async login(credentials) {
    try {
      const response = await fetch(API_ENDPOINTS.auth.login, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(credentials)
      });

      // If response is not ok, parse the error message
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        const errorMessage = errorData.message || 'An error occurred during login';
        const errorCode = errorData.code || 'UNKNOWN_ERROR';
        
        // Create a structured error object with additional properties
        const error = new Error(errorMessage);
        error.code = errorCode;
        error.status = response.status;
        throw error;
      }

      const data = await response.json();
      
      if (data.token) {
        this.token = data.token;
        this.refreshToken = data.refreshToken;
        this.user = data.user;
        
        // Persist tokens to localStorage
        this.saveTokensToStorage();
        
        this.notifyListeners({ type: 'login', user: data.user });
        return { success: true, user: data.user };
      }
      
      throw new Error('Login failed: Invalid response');
    } catch (error) {
      console.error('Login error:', error);
      throw error;
    }
  }

  /**
   * Attempts to signup user
   * @param {Object} userData - User signup data
   * @returns {Promise<Object>} Signup result
   */
  async signup(userData) {
    try {
      const response = await fetch(API_ENDPOINTS.auth.signup, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(userData)
      });

      // If response is not ok, parse the error message
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        const errorMessage = errorData.message || 'An error occurred during signup';
        const errorCode = errorData.code || 'UNKNOWN_ERROR';
        
        // Create a structured error object with additional properties
        const error = new Error(errorMessage);
        error.code = errorCode;
        error.status = response.status;
        throw error;
      }

      const data = await response.json();
      
      if (data.token) {
        this.token = data.token;
        this.refreshToken = data.refreshToken;
        this.user = data.user;
        
        // Persist tokens to localStorage
        this.saveTokensToStorage();
        
        this.notifyListeners({ type: 'signup', user: data.user });
        return { success: true, user: data.user };
      }
      
      throw new Error('Signup failed: Invalid response');
    } catch (error) {
      console.error('Signup error:', error);
      throw error;
    }
  }

  /**
   * Logs out user and clears session
   * @returns {Promise<boolean>} Logout success
   */
  async logout() {
    try {
      if (this.token) {
        await fetch(API_ENDPOINTS.auth.logout, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${this.token}`
          }
        });
      }
    } catch (error) {
      console.error('Logout error:', error);
    } finally {
      this.token = null;
      this.refreshToken = null;
      this.user = null;
      
      // Clear persisted tokens
      this.clearTokensFromStorage();
      
      this.notifyListeners({ type: 'logout' });
    }
  }

  /**
   * Gets current auth state from secure storage
   * @returns {Promise<Object>} Current auth state
   */
  async verifySession() {
    try {
      // Load tokens from storage if not in memory
      if (!this.token) {
        this.loadTokensFromStorage();
      }
      
      if (!this.token) return null;

      const response = await fetch(API_ENDPOINTS.auth.verify, {
        headers: {
          'Authorization': `Bearer ${this.token}`
        }
      });

      const data = await handleApiResponse(response);
      
      if (data.user) {
        this.user = data.user;
        this.saveTokensToStorage();
        return data.user;
      }
      
      return null;
    } catch (error) {
      console.error('Session verification failed:', error);
      this.token = null;
      this.refreshToken = null;
      this.user = null;
      this.clearTokensFromStorage();
      return null;
    }
  }
  
  /**
   * Check if user is authenticated
   * @returns {boolean} True if user has valid token
   */
  isAuthenticated() {
    if (!this.token) {
      this.loadTokensFromStorage();
    }
    return !!this.token;
  }

  /**
   * Get current user data
   * @returns {Object|null} Current user data
   */
  getCurrentUser() {
    if (!this.user) {
      this.loadTokensFromStorage();
    }
    return this.user;
  }

  // Event listener methods remain unchanged
  addListener(callback) {
    this.listeners.add(callback);
  }
  
  removeListener(callback) {
    this.listeners.delete(callback);
  }
  
  notifyListeners(data) {
    this.listeners.forEach(callback => {
      try {
        callback(data);
      } catch (error) {
        console.error('Error in session listener:', error);
      }
    });
  }
  
  init() {
    // Load persisted session on init
    this.loadTokensFromStorage();
    
    // Auto-verify session if token exists
    if (this.token) {
      this.verifySession().then(user => {
        if (user) {
          this.notifyListeners({ type: 'restore_session', user });
        }
      }).catch(console.error);
    }

    // Listen for beforeunload to clean up
    window.addEventListener('beforeunload', () => {
      this.notifyListeners({ type: 'before_unload' });
    });
  }
}

// Create singleton instance
export const sessionManager = new SessionManager();

// Auto-initialize if in browser environment
if (typeof window !== 'undefined') {
  sessionManager.init();
}