/**
 * CV Upload Service - API integration with cv-upload-service backend
 */

// Determine CV upload service URL
const getCVUploadServiceURL = () => {
  const isProduction = typeof window !== 'undefined' && 
    (window.location.hostname === 'leochlon.github.io' || 
     window.location.hostname.includes('github.io'));
  
  return isProduction 
    ? 'https://ai-careers-backend.fly.dev' // Production backend (same as main API)
    : (process.env.REACT_APP_API_URL || 'http://localhost:5002'); // Local development (same as main API)
};

const CV_UPLOAD_API_URL = getCVUploadServiceURL();

console.log('Using CV Upload Service URL:', CV_UPLOAD_API_URL);

export const CV_UPLOAD_ENDPOINTS = {
  upload: `${CV_UPLOAD_API_URL}/api/upload`,
  submissions: {
    list: `${CV_UPLOAD_API_URL}/api/upload/submissions`,
    count: `${CV_UPLOAD_API_URL}/api/upload/submissions/count`,
    delete: (id) => `${CV_UPLOAD_API_URL}/api/upload/submissions/${id}`,
    download: (id) => `${CV_UPLOAD_API_URL}/api/upload/submissions/${id}/download`
  }
};

/**
 * Get authentication headers for CV upload service
 */
const getAuthHeaders = (token, isMultipart = false) => {
  const headers = {};
  
  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }
  
  // Don't set Content-Type for multipart/form-data - browser will set it with boundary
  if (!isMultipart) {
    headers['Content-Type'] = 'application/json';
  }
  
  return headers;
};

/**
 * Handle API response errors
 */
const handleApiResponse = async (response) => {
  if (!response.ok) {
    let errorMessage = 'An error occurred while processing your request';
    let errorData = {};
    
    try {
      errorData = await response.json();
      errorMessage = errorData.detail || errorData.message || errorData.error || errorMessage;
    } catch (e) {
      // If we can't parse JSON, use status text
      errorMessage = response.statusText || errorMessage;
    }
    
    console.error(`API Error (${response.status}):`, errorMessage, response.url);
    
    // Create a structured error with additional properties
    const error = new Error(errorMessage);
    error.status = response.status;
    error.url = response.url;
    error.data = errorData;
    
    throw error;
  }
  
  try {
    return await response.json();
  } catch (e) {
    console.warn('Could not parse JSON from response', e);
    return { success: true }; // Return a basic success response if parsing fails
  }
};

/**
 * Get CSRF headers for state-changing operations
 * 
 * @param {string} authToken - The authentication token
 * @param {boolean} isMultipart - Whether this is a multipart/form-data request
 * @returns {Promise<Object>} - Headers with CSRF token
 */
const getCSRFHeaders = async (authToken, isMultipart = false) => {
  try {
    // Get the base headers - we'll use these regardless of whether CSRF is available
    let headers = getAuthHeaders(authToken, isMultipart);
    
    try {
      // Dynamically import csrfService to avoid circular dependencies
      const { default: csrfService } = await import('./csrfService.js');
      
      // Get CSRF token and add it to headers
      const csrfToken = await csrfService.getCSRFToken();
      if (csrfToken) {
        headers['X-CSRF-Token'] = csrfToken;
      }
    } catch (csrfError) {
      console.warn('CSRF token not available, proceeding without it:', csrfError);
      // Continue without CSRF token - the API may not require it
    }
    
    return headers;
  } catch (error) {
    console.error('Error getting CSRF headers:', error);
    // Fallback to regular auth headers if anything fails
    return getAuthHeaders(authToken, isMultipart);
  }
};

/**
 * Upload CV and job description
 */
export const uploadCV = async (cvFile, jobDescription, targetRole, authToken) => {
  if (!authToken) {
    throw new Error('Authentication required');
  }
  
  // Validate file type
  if (!cvFile.type.includes('pdf')) {
    throw new Error('Only PDF files are allowed');
  }
  
  // Validate file size (10MB limit)
  const maxSize = 10 * 1024 * 1024; // 10MB
  if (cvFile.size > maxSize) {
    throw new Error('File size must be less than 10MB');
  }
  
  // Validate job description
  if (!jobDescription || jobDescription.trim().length < 10) {
    throw new Error('Job description must be at least 10 characters long');
  }
  
  const formData = new FormData();
  formData.append('cv', cvFile);
  formData.append('job_description', jobDescription.trim());
  
  if (targetRole && targetRole.trim()) {
    formData.append('target_role', targetRole.trim());
  }
  
  try {
    // Get CSRF headers
    const headers = await getCSRFHeaders(authToken, true); // multipart = true
    
    const response = await fetch(CV_UPLOAD_ENDPOINTS.upload, {
      method: 'POST',
      headers: headers,
      body: formData
    });
    
    return handleApiResponse(response);
  } catch (error) {
    console.error('CV upload error:', error);
    throw error;
  }
};

/**
 * Get user's submission count
 */
export const getSubmissionCount = async (authToken) => {
  if (!authToken) {
    throw new Error('Authentication required');
  }
  
  try {
    const response = await fetch(CV_UPLOAD_ENDPOINTS.submissions.count, {
      method: 'GET',
      headers: getAuthHeaders(authToken)
    });
    
    return handleApiResponse(response);
  } catch (error) {
    console.error('Get submission count error:', error);
    // Return a fallback response instead of throwing
    return { submission_count: 0, error: error.message };
  }
};

/**
 * Get user's submission history
 */
export const getSubmissionHistory = async (authToken) => {
  if (!authToken) {
    throw new Error('Authentication required');
  }
  
  try {
    const response = await fetch(CV_UPLOAD_ENDPOINTS.submissions.list, {
      method: 'GET',
      headers: getAuthHeaders(authToken)
    });
    
    return handleApiResponse(response);
  } catch (error) {
    console.error('Get submission history error:', error);
    // Return a fallback response instead of throwing
    return { submissions: [], error: error.message };
  }
};

/**
 * Delete a submission
 */
export const deleteSubmission = async (submissionId, authToken) => {
  if (!authToken) {
    throw new Error('Authentication required');
  }
  
  if (!submissionId) {
    throw new Error('Submission ID is required');
  }
  
  try {
    // Get CSRF headers for this state-changing operation
    const headers = await getCSRFHeaders(authToken);
    
    const response = await fetch(CV_UPLOAD_ENDPOINTS.submissions.delete(submissionId), {
      method: 'DELETE',
      headers: headers
    });
    
    return handleApiResponse(response);
  } catch (error) {
    console.error('Delete submission error:', error);
    throw error;
  }
};

/**
 * Download a CV submission
 */
export const downloadSubmission = async (submissionId, authToken) => {
  if (!authToken) {
    throw new Error('Authentication required');
  }
  
  if (!submissionId) {
    throw new Error('Submission ID is required');
  }
  
  try {
    const response = await fetch(CV_UPLOAD_ENDPOINTS.submissions.download(submissionId), {
      method: 'GET',
      headers: getAuthHeaders(authToken)
    });
    
    if (!response.ok) {
      throw new Error('Failed to download CV submission');
    }
    
    return response.blob();
  } catch (error) {
    console.error('Download submission error:', error);
    throw error;
  }
};

/**
 * Validate PDF file before upload
 */
export const validatePDFFile = (file) => {
  const errors = [];
  
  // Check file type
  if (!file.type.includes('pdf') && !file.name.toLowerCase().endsWith('.pdf')) {
    errors.push('Only PDF files are allowed');
  }
  
  // Check file size (10MB limit)
  const maxSize = 10 * 1024 * 1024; // 10MB
  if (file.size > maxSize) {
    errors.push('File size must be less than 10MB');
  }
  
  // Check file size minimum (1KB)
  if (file.size < 1024) {
    errors.push('File is too small - please select a valid PDF');
  }
  
  return {
    isValid: errors.length === 0,
    errors
  };
};

/**
 * Format file size for display
 */
export const formatFileSize = (bytes) => {
  if (bytes === 0) return '0 Bytes';
  
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

/**
 * Check if submission limit reached
 */
export const isSubmissionLimitReached = (submissionCount) => {
  return submissionCount >= 3;
};

/**
 * Get remaining submissions
 */
export const getRemainingSubmissions = (submissionCount) => {
  return Math.max(0, 3 - submissionCount);
};

export default {
  uploadCV,
  getSubmissionCount,
  getSubmissionHistory,
  deleteSubmission,
  downloadSubmission,
  validatePDFFile,
  formatFileSize,
  isSubmissionLimitReached,
  getRemainingSubmissions
};