import React from 'react';
import styled, { keyframes, css } from 'styled-components';
import { motion } from 'framer-motion';

const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

const ProgressContainer = styled(motion.div)`
  display: flex;
  flex-direction: column;
  gap: ${props => props.theme.spacing.md};
  padding: ${props => props.theme.spacing.lg};
  background: ${props => props.theme.colors.glass.primary};
  border: 1px solid ${props => props.theme.colors.glass.border};
  border-radius: ${props => props.theme.borderRadius.lg};
  backdrop-filter: blur(20px);
  font-family: ${props => props.theme.typography.fontFamily.primary};
`;

const ProgressHeader = styled.div`
  display: flex;
  align-items: center;
  gap: ${props => props.theme.spacing.md};
`;

const ProgressIcon = styled.div`
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  ${props => props.$isLoading && css`
    animation: ${spin} 1s linear infinite;
  `}
`;

const ProgressText = styled.div`
  flex: 1;
`;

const ProgressTitle = styled.h4`
  margin: 0;
  color: ${props => props.theme.colors.text.primary};
  font-size: ${props => props.theme.typography.fontSize.base};
  font-weight: ${props => props.theme.typography.fontWeight.medium};
`;

const ProgressSubtitle = styled.p`
  margin: ${props => props.theme.spacing.xs} 0 0 0;
  color: ${props => props.theme.colors.text.secondary};
  font-size: ${props => props.theme.typography.fontSize.sm};
`;

const ProgressBarContainer = styled.div`
  width: 100%;
  height: 8px;
  background: ${props => props.theme.colors.glass.secondary};
  border-radius: ${props => props.theme.borderRadius.full};
  overflow: hidden;
  position: relative;
`;

const shimmer = keyframes`
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
`;

const ProgressBar = styled(motion.div)`
  height: 100%;
  background: ${props => 
    props.$status === 'error' 
      ? '#ff6b6b'
      : props.$status === 'success'
      ? props.theme.colors.text.accent
      : `linear-gradient(90deg, ${props.theme.colors.text.accent}, #8b5cf6)`
  };
  border-radius: ${props => props.theme.borderRadius.full};
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.3),
      transparent
    );
    animation: ${props => props.$isAnimated ? css`${shimmer} 1.5s infinite` : 'none'};
  }
`;

const StatusIcon = styled.span`
  font-size: 18px;
`;

const ErrorMessage = styled.div`
  color: #ff6b6b;
  font-size: ${props => props.theme.typography.fontSize.sm};
  margin-top: ${props => props.theme.spacing.xs};
`;

const SuccessMessage = styled.div`
  color: ${props => props.theme.colors.text.accent};
  font-size: ${props => props.theme.typography.fontSize.sm};
  margin-top: ${props => props.theme.spacing.xs};
`;

const getStatusIcon = (status) => {
  switch (status) {
    case 'uploading':
      return '';
    case 'processing':
      return '';
    case 'success':
      return '';
    case 'error':
      return '';
    default:
      return '';
  }
};

const getStatusText = (status, fileName) => {
  switch (status) {
    case 'uploading':
      return {
        title: 'Uploading CV...',
        subtitle: `Uploading ${fileName || 'file'}`
      };
    case 'processing':
      return {
        title: 'Processing submission...',
        subtitle: 'Validating file and saving data'
      };
    case 'success':
      return {
        title: 'Upload successful!',
        subtitle: 'Your CV has been submitted successfully'
      };
    case 'error':
      return {
        title: 'Upload failed',
        subtitle: 'Please try again or contact support'
      };
    default:
      return {
        title: 'Ready to upload',
        subtitle: 'Select a PDF file to get started'
      };
  }
};

const UploadProgress = ({ 
  status = 'idle', 
  progress = 0, 
  fileName,
  error,
  success,
  className 
}) => {
  const statusText = getStatusText(status, fileName);
  const isLoading = status === 'uploading' || status === 'processing';
  const showProgress = status !== 'idle';

  return (
    <ProgressContainer
      className={className}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
    >
      <ProgressHeader>
        <ProgressIcon $isLoading={isLoading}>
          <StatusIcon>{getStatusIcon(status)}</StatusIcon>
        </ProgressIcon>
        
        <ProgressText>
          <ProgressTitle>{statusText.title}</ProgressTitle>
          <ProgressSubtitle>{statusText.subtitle}</ProgressSubtitle>
        </ProgressText>
      </ProgressHeader>

      {showProgress && (
        <ProgressBarContainer>
          <ProgressBar
            $status={status}
            $isAnimated={isLoading}
            initial={{ width: 0 }}
            animate={{ width: `${progress}%` }}
            transition={{ 
              duration: status === 'success' ? 0.5 : 0.3,
              ease: 'easeOut'
            }}
          />
        </ProgressBarContainer>
      )}

      {error && (
        <ErrorMessage>
          {error}
        </ErrorMessage>
      )}

      {success && status === 'success' && (
        <SuccessMessage>
          {success}
        </SuccessMessage>
      )}
    </ProgressContainer>
  );
};

export default UploadProgress;