import { useEffect, useRef } from 'react';

export const useSwipeNavigation = ({ 
  currentSection, 
  totalSections, 
  onNavigate, 
  isModalOpen = false,
  threshold = 50 
}) => {
  const touchStart = useRef(null);
  const touchEnd = useRef(null);

  // Minimum distance required to trigger swipe
  const minSwipeDistance = threshold;

  const onTouchStart = (e) => {
    if (isModalOpen) return;
    touchEnd.current = null; // Reset end position
    touchStart.current = e.targetTouches[0].clientX;
  };

  const onTouchMove = (e) => {
    if (isModalOpen) return;
    touchEnd.current = e.targetTouches[0].clientX;
  };

  const onTouchEnd = () => {
    if (isModalOpen) return;
    if (!touchStart.current || !touchEnd.current) return;
    
    const distance = touchStart.current - touchEnd.current;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe && currentSection < totalSections - 1) {
      // Swipe left = next section
      onNavigate(currentSection + 1);
    }
    
    if (isRightSwipe && currentSection > 0) {
      // Swipe right = previous section
      onNavigate(currentSection - 1);
    }
  };

  useEffect(() => {
    const isMobile = window.innerWidth <= 768;
    if (!isMobile) return;

    document.addEventListener('touchstart', onTouchStart);
    document.addEventListener('touchmove', onTouchMove);
    document.addEventListener('touchend', onTouchEnd);

    return () => {
      document.removeEventListener('touchstart', onTouchStart);
      document.removeEventListener('touchmove', onTouchMove);
      document.removeEventListener('touchend', onTouchEnd);
    };
  }, [currentSection, totalSections, onNavigate, isModalOpen]);
};