const checklistItems = [
  "Use simple, ATS-friendly resume format (avoid complex layouts)",
  "Include exact keywords from job description",
  "Use standard job titles and section headers",
  "Add specific metrics and quantified achievements",
  "List relevant technical skills with specific tools/technologies",
  "Keep formatting consistent (fonts, spacing, alignment)",
  "Include relevant certifications and education",
  "Use bullet points instead of paragraphs",
  "Save as .docx format (not PDF unless specifically requested)",
  "Tailor resume for each specific job application"
];

const CommonMistakes = () => {
  const [selectedExamples, setSelectedExamples] = useState({});
  const [checkedItems, setCheckedItems] = useState({});
  const [expandedCards, setExpandedCards] = useState({});

  const toggleExample = (mistakeId, type) => {
    setSelectedExamples(prev => ({
      ...prev,
      [mistakeId]: type
    }));
  };

  const toggleChecklist = (index) => {
    setCheckedItems(prev => ({
      ...prev,
      [index]: !prev[index]
    }));
  };

  const toggleCard = (mistakeId) => {
    setExpandedCards(prev => ({
      ...prev,
      [mistakeId]: !prev[mistakeId]
    }));
  };

  useEffect(() => {
    // Initialize with "wrong" examples showing
    const initialExamples = {};
    mistakesData.forEach(mistake => {
      initialExamples[mistake.id] = 'wrong';
    });
    setSelectedExamples(initialExamples);
  }, []);

  const getCheckedPercentage = () => {
    const checkedCount = Object.values(checkedItems).filter(Boolean).length;
    return Math.round((checkedCount / checklistItems.length) * 100);
  };

  return (
    <SectionWrapper>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           Common Resume Mistakes
        </Title>
        <Subtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          The critical errors that get your resume rejected by ATS systems
        </Subtitle>
      </Header>

      <MistakesGrid>
        {mistakesData.map((mistake, index) => (
          <MistakeCard
            key={mistake.id}
            severity={mistake.severity}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            onClick={() => toggleCard(mistake.id)}
          >
            <MistakeHeader>
              <span className="icon">{mistake.icon}</span>
              <span className="title">{mistake.title}</span>
              <SeverityBadge severity={mistake.severity}>
                {mistake.severity}
              </SeverityBadge>
            </MistakeHeader>

            <MistakeDescription>
              {mistake.description}
            </MistakeDescription>

            <ExampleContainer>
              <ExampleTab>
                <TabButton
                  type="wrong"
                  active={selectedExamples[mistake.id] === 'wrong'}
                  onClick={(e) => {
                    e.stopPropagation();
                    toggleExample(mistake.id, 'wrong');
                  }}
                >
                   Wrong
                </TabButton>
                <TabButton
                  type="right"
                  active={selectedExamples[mistake.id] === 'right'}
                  onClick={(e) => {
                    e.stopPropagation();
                    toggleExample(mistake.id, 'right');
                  }}
                >
                   Right
                </TabButton>
              </ExampleTab>

              <AnimatePresence mode="wait">
                <ExampleContent
                  key={`${mistake.id}-${selectedExamples[mistake.id]}`}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                  transition={{ duration: 0.2 }}
                >
                  {selectedExamples[mistake.id] === 'wrong' 
                    ? mistake.wrongExample 
                    : mistake.rightExample}
                </ExampleContent>
              </AnimatePresence>
            </ExampleContainer>

            <ImpactStats>
              {Object.entries(mistake.impact).map(([key, value]) => (
                <ImpactStat
                  key={key}
                  color={
                    mistake.severity === 'critical' ? '#ff6b6b' : 
                    mistake.severity === 'high' ? '#ff9a00' : '#ffd700'
                  }
                >
                  <div className="number">{value}</div>
                  <div className="label">{key}</div>
                </ImpactStat>
              ))}
            </ImpactStats>
          </MistakeCard>
        ))}
      </MistakesGrid>

      <ChecklistContainer>
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          style={{ 
            textAlign: 'center', 
            marginBottom: '2rem',
            color: '#e0e0e0',
            fontSize: '2rem'
          }}
        >
          ATS-Friendly Resume Checklist
        </motion.h2>

        <motion.div
          style={{
            textAlign: 'center',
            marginBottom: '2rem',
            fontSize: '1.5rem',
            color: getCheckedPercentage() > 70 ? '#00ffe7' : '#ff9a00'
          }}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
        >
          Completion: {getCheckedPercentage()}%
        </motion.div>

        {checklistItems.map((item, index) => (
          <ChecklistItem
            key={index}
            checked={checkedItems[index]}
            onClick={() => toggleChecklist(index)}
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ delay: index * 0.05 }}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <Checkbox checked={checkedItems[index]}>
              {checkedItems[index] ? '' : ''}
            </Checkbox>
            <ChecklistText>{item}</ChecklistText>
          </ChecklistItem>
        ))}
      </ChecklistContainer>
    </SectionWrapper>
  );
};

export default CommonMistakes;