const categories = [
  { id: "all", label: "All Tools" },
  { id: "optimization", label: "Optimization" },
  { id: "analysis", label: "Analysis" },
  { id: "review", label: "Review" },
  { id: "builder", label: "Builder" },
  { id: "automation", label: "Automation" }
];

const AIResumeTools = () => {
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [visibleTools, setVisibleTools] = useState([]);

  useEffect(() => {
    const filtered = selectedCategory === "all" 
      ? toolsData 
      : toolsData.filter(tool => tool.category === selectedCategory);
    setVisibleTools(filtered);
  }, [selectedCategory]);

  const handleToolClick = (tool) => {
    // Simulate opening tool website
    console.log(`Opening ${tool.name}...`);
  };

  return (
    <SectionWrapper>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           AI-Driven Resume Tools
        </Title>
        <Subtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          Essential tools to optimize your resume for ATS systems and get more interviews
        </Subtitle>
      </Header>

      <FilterContainer>
        {categories.map((category) => (
          <FilterButton
            key={category.id}
            active={selectedCategory === category.id}
            onClick={() => setSelectedCategory(category.id)}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            {category.label}
          </FilterButton>
        ))}
      </FilterContainer>

      <ToolsGrid>
        <AnimatePresence mode="wait">
          {visibleTools.map((tool, index) => (
            <ToolCard
              key={tool.id}
              color={tool.color}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3, delay: index * 0.1 }}
              onClick={() => handleToolClick(tool)}
              whileHover={{ scale: 1.02 }}
            >
              <ToolHeader
                logoColor={tool.logoColor}
                pricingColor={tool.pricingColor}
                pricingTextColor={tool.pricingTextColor}
              >
                <div className="logo">{tool.icon}</div>
                <div className="info">
                  <div className="name">{tool.name}</div>
                  <div className="tagline">{tool.tagline}</div>
                  <div className="pricing">{tool.pricing}</div>
                </div>
              </ToolHeader>

              <div style={{ 
                color: '#a0a0a0', 
                marginBottom: '1.5rem',
                lineHeight: 1.5 
              }}>
                {tool.description}
              </div>

              <FeaturesList>
                {tool.features.map((feature, idx) => (
                  <Feature
                    key={idx}
                    color={feature.color}
                    initial={{ opacity: 0, x: -10 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: 0.2 + idx * 0.05 }}
                  >
                    <span className="icon">{feature.icon}</span>
                    <span className="text">{feature.text}</span>
                  </Feature>
                ))}
              </FeaturesList>

              <StatsContainer>
                {Object.entries(tool.stats).map(([key, value]) => (
                  <Stat key={key} color={tool.color}>
                    <div className="number">{value}</div>
                    <div className="label">{key}</div>
                  </Stat>
                ))}
              </StatsContainer>

              <CTAButton
                color={tool.color}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                {tool.cta}
              </CTAButton>
            </ToolCard>
          ))}
        </AnimatePresence>
      </ToolsGrid>

      <SuccessStories>
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          style={{ 
            textAlign: 'center', 
            marginBottom: '2rem',
            color: '#e0e0e0',
            fontSize: '2rem'
          }}
        >
           Success Stories
        </motion.h2>

        {successStories.map((story, index) => (
          <StoryCard
            key={index}
            color={story.color}
            initial={{ opacity: 0, x: index % 2 === 0 ? -50 : 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ delay: index * 0.2 }}
          >
            <div className="quote">"{story.quote}"</div>
            <div className="author"> {story.author}</div>
            <div className="results">
              {story.results.map((result, idx) => (
                <div key={idx} className="result">
                  <div className="number">{result.number}</div>
                  <div className="label">{result.label}</div>
                </div>
              ))}
            </div>
          </StoryCard>
        ))}
      </SuccessStories>
    </SectionWrapper>
  );
};

export default AIResumeTools;