import { useState, useEffect, useCallback } from 'react';
import { useUser } from '../context/UserContext';
import { 
  sessionManager,
  backupNotes, 
  exportUserData,
  STORAGE_KEYS,
  safeLocalStorageGet 
} from '../utils/sessionManager';

/**
 * Custom hook for managing user session with authentication
 * @returns {Object} Session management functions and state
 */
export const useUserSession = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  
  const {
    user,
    setUser,
    createUserProfile,
    updateProfile,
    setContentUnlocked,
    resetUser,
    addNote,
    updateNote,
    deleteNote,
    getSectionNotes
  } = useUser();

  // Auto-backup notes whenever they change
  useEffect(() => {
    if (user.notes && Object.keys(user.notes).length > 0) {
      backupNotes(user.notes);
    }
  }, [user.notes]);

  // Verify session on mount
  useEffect(() => {
    const verifySession = async () => {
      try {
        setIsLoading(true);
        const verifiedUser = await sessionManager.verifySession();
        if (verifiedUser) {
          setUser(verifiedUser);
        }
      } catch (err) {
        console.error('Session verification failed:', err);
        setError('Failed to verify session');
      } finally {
        setIsLoading(false);
      }
    };

    // Only attempt to verify session if not already authenticated
    if (!user.id) {
      verifySession();
    }
  }, [setUser, user.id]);

  // Listen for auth state changes
  useEffect(() => {
    const handleAuthChange = (data) => {
      switch (data.type) {
        case 'login':
        case 'signup':
          setUser(data.user);
          break;
        case 'logout':
          resetUser();
          break;
        default:
          break;
      }
    };

    sessionManager.addListener(handleAuthChange);
    return () => sessionManager.removeListener(handleAuthChange);
  }, [setUser, resetUser]);

  // Enhanced auth methods with error handling
  const login = useCallback(async (credentials) => {
    setError(null);
    try {
      setIsLoading(true);
      // Always remember email for convenience
      if (credentials.email) {
        localStorage.setItem('aiCareersRememberedEmail', credentials.email);
      }
      const result = await sessionManager.login(credentials);
      return result;
    } catch (err) {
      // Extract more detailed error information
      const errorMessage = getAuthErrorMessage(err);
      setError(errorMessage);
      throw err;
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Helper function to translate error codes to user-friendly messages
  const getAuthErrorMessage = (error) => {
    // Default error message
    let message = 'Authentication failed. Please try again.';
    
    // If the error has a code property, use it to provide a more specific message
    if (error.code) {
      switch (error.code) {
        case 'INVALID_CREDENTIALS':
          message = 'Invalid email or password. Please try again.';
          break;
        case 'ACCOUNT_LOCKED':
          message = 'Your account has been temporarily locked due to multiple failed attempts. Please try again later.';
          break;
        case 'EMAIL_NOT_VERIFIED':
          message = 'Please verify your email address before logging in.';
          break;
        case 'EMAIL_ALREADY_REGISTERED':
          message = 'This email address is already registered. Please use a different email or try logging in.';
          break;
        default:
          // If we have a message from the server, use it
          message = error.message || message;
      }
    } else if (error.message) {
      // Use error message if available
      message = error.message;
    }
    
    return message;
  };

  const signup = useCallback(async (userData) => {
    setError(null);
    try {
      setIsLoading(true);
      // Always remember email for convenience
      if (userData.email) {
        localStorage.setItem('aiCareersRememberedEmail', userData.email);
      }
      const result = await sessionManager.signup(userData);
      return result;
    } catch (err) {
      const errorMessage = getAuthErrorMessage(err);
      setError(errorMessage);
      throw err;
    } finally {
      setIsLoading(false);
    }
  }, []);

  const logout = useCallback(async () => {
    try {
      await sessionManager.logout();
      resetUser();
    } catch (err) {
      console.error('Logout error:', err);
    }
  }, [resetUser]);

  // Export user data
  const exportSession = useCallback(() => {
    try {
      const exportData = exportUserData(user);
      const blob = new Blob([exportData], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      
      const link = document.createElement('a');
      link.href = url;
      link.download = `ai-careers-profile-${user.name || 'user'}-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      URL.revokeObjectURL(url);
      return true;
    } catch (error) {
      console.error('Error exporting session:', error);
      return false;
    }
  }, [user]);

  // Import user data
  const importSession = useCallback((file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = (e) => {
        try {
          const importData = JSON.parse(e.target.result);
          
          if (importData.userData && importData.userData.id) {
            // Validate the data structure
            const requiredFields = ['id', 'email', 'name'];
            const isValid = requiredFields.every(field => 
              importData.userData.hasOwnProperty(field)
            );
            
            if (isValid) {
              updateProfile(importData.userData);
              resolve(importData.userData);
            } else {
              reject(new Error('Invalid profile data structure'));
            }
          } else {
            reject(new Error('No valid user data found in file'));
          }
        } catch (error) {
          reject(new Error('Invalid JSON file'));
        }
      };
      
      reader.onerror = () => reject(new Error('Error reading file'));
      reader.readAsText(file);
    });
  }, [updateProfile]);

  // Update last activity timestamp
  const updateActivity = useCallback(() => {
    updateProfile({ lastActivity: new Date().toISOString() });
  }, [updateProfile]);

  // Enhanced note management with activity tracking
  const addNoteWithActivity = useCallback((sectionId, content) => {
    addNote(sectionId, content);
    updateActivity();
  }, [addNote, updateActivity]);

  const updateNoteWithActivity = useCallback((sectionId, noteId, content) => {
    updateNote(sectionId, noteId, content);
    updateActivity();
  }, [updateNote, updateActivity]);

  const deleteNoteWithActivity = useCallback((sectionId, noteId) => {
    deleteNote(sectionId, noteId);
    updateActivity();
  }, [deleteNote, updateActivity]);

  // Check if session can be restored from backup
  const canRestoreSession = useCallback(() => {
    const backupData = safeLocalStorageGet(STORAGE_KEYS.NOTES_BACKUP);
    return !!(backupData && backupData.notes);
  }, []);

  // Restore session from backup
  const restoreFromBackup = useCallback(() => {
    try {
      const backupData = safeLocalStorageGet(STORAGE_KEYS.NOTES_BACKUP);
      if (backupData && backupData.notes) {
        updateProfile({ notes: backupData.notes });
        return true;
      }
      return false;
    } catch (error) {
      console.error('Error restoring from backup:', error);
      return false;
    }
  }, [updateProfile]);

  return {
    // Auth state
    isAuthenticated: !!user.id,
    isLoading,
    error,
    
    // Auth methods
    login,
    signup,
    logout,
    
    // User state
    user,
    hasProfile: user.hasProfile,
    isContentUnlocked: user.isContentUnlocked,
    
    // Profile management
    createUserProfile,
    updateProfile,
    setContentUnlocked,
    resetUser,
    
    // Note management
    addNote: addNoteWithActivity,
    updateNote: updateNoteWithActivity,
    deleteNote: deleteNoteWithActivity,
    getSectionNotes,
    
    // Session management
    exportSession,
    importSession,
    updateActivity,
    
    // Backup and restore
    canRestoreSession,
    restoreFromBackup
  };
};

export default useUserSession;