/**
 * Authentication Integration for CV Upload Service
 * Handles token management and session validation for CV upload features
 */

import { sessionManager } from '../utils/sessionManager';

/**
 * Get current authentication token
 */
export const getAuthToken = () => {
  return sessionManager.token;
};

/**
 * Check if user is authenticated
 */
export const isAuthenticated = () => {
  return sessionManager.isAuthenticated();
};

/**
 * Get current user data
 */
export const getCurrentUser = () => {
  return sessionManager.getCurrentUser();
};

/**
 * Validate current session and refresh token if needed
 */
export const validateSession = async () => {
  try {
    if (!sessionManager.isAuthenticated()) {
      throw new Error('User not authenticated');
    }
    
    // Try to verify session with backend
    const verifiedUser = await sessionManager.verifySession();
    
    if (!verifiedUser) {
      throw new Error('Session validation failed');
    }
    
    return {
      isValid: true,
      user: verifiedUser,
      token: sessionManager.token
    };
  } catch (error) {
    console.error('Session validation error:', error);
    return {
      isValid: false,
      error: error.message
    };
  }
};

/**
 * Handle authentication errors from CV upload service
 */
export const handleAuthError = async (error) => {
  console.warn('CV Upload Service auth error:', error.message);
  
  // If it's a server error (500), don't treat it as an auth issue
  if (error.status === 500) {
    console.error('Server error (500) detected in handleAuthError, not treating as auth issue');
    throw error;
  }
  
  // Check if it's an authentication error
  if (error.status === 401 || 
      error.message.includes('401') || 
      error.message.includes('Unauthorized') || 
      error.message.includes('Authentication required')) {
    
    // Try to refresh session
    try {
      const sessionValidation = await validateSession();
      if (sessionValidation.isValid) {
        return sessionValidation.token;
      }
    } catch (refreshError) {
      console.error('Failed to refresh session:', refreshError);
    }
    
    // If refresh fails, clear session and redirect to login
    await sessionManager.logout();
    
    // Trigger a page refresh or redirect to login
    if (typeof window !== 'undefined') {
      window.location.reload();
    }
    
    throw new Error('Session expired. Please log in again.');
  }
  
  // Re-throw non-auth errors
  throw error;
};

/**
 * Retry API call with fresh token if authentication fails
 */
export const withAuthRetry = async (apiCall, maxRetries = 1) => {
  let attempts = 0;
  
  while (attempts <= maxRetries) {
    try {
      const token = getAuthToken();
      if (!token) {
        throw new Error('Authentication required');
      }
      
      return await apiCall(token);
    } catch (error) {
      attempts++;
      
      // Log error details for debugging
      console.warn(`API call error (attempt ${attempts}/${maxRetries+1}):`, 
        error.message, 
        error.status ? `Status: ${error.status}` : '',
        error.url ? `URL: ${error.url}` : ''
      );
      
      // If it's a server error (500), don't retry as it's likely not an auth issue
      if (error.status === 500) {
        console.error('Server error (500) detected, not retrying:', error.message);
        throw error;
      }
      
      // If it's an auth error and we haven't exhausted retries
      if (attempts <= maxRetries && 
          (error.status === 401 || 
           error.message.includes('401') || 
           error.message.includes('Unauthorized'))) {
        
        console.log(`Auth error on attempt ${attempts}, retrying...`);
        
        try {
          // Try to refresh session
          const sessionValidation = await validateSession();
          if (sessionValidation.isValid) {
            continue; // Retry with fresh token
          }
        } catch (refreshError) {
          console.error('Failed to refresh session during retry:', refreshError);
        }
      }
      
      // Handle auth error or re-throw if not auth related
      await handleAuthError(error);
    }
  }
};

/**
 * Get user permissions for CV upload features
 */
export const getUserPermissions = () => {
  const user = getCurrentUser();
  
  if (!user) {
    return {
      canUpload: false,
      canViewSubmissions: false,
      canDeleteSubmissions: false,
      isAdmin: false
    };
  }
  
  return {
    canUpload: true,
    canViewSubmissions: true,
    canDeleteSubmissions: true,
    isAdmin: user.isAdmin || false
  };
};

/**
 * Check if user has admin permissions
 */
export const isAdmin = () => {
  const user = getCurrentUser();
  return user?.isAdmin === true;
};

/**
 * Format user info for CV upload service
 */
export const getUserInfo = () => {
  const user = getCurrentUser();
  
  if (!user) {
    return null;
  }
  
  return {
    id: user.id,
    email: user.email,
    name: user.name || user.email,
    isAdmin: user.isAdmin || false
  };
};

/**
 * Ensure user is authenticated before accessing CV upload features
 */
export const requireAuth = () => {
  if (!isAuthenticated()) {
    throw new Error('Authentication required to access CV upload features');
  }
  
  const token = getAuthToken();
  if (!token) {
    throw new Error('Valid authentication token required');
  }
  
  return token;
};

export default {
  getAuthToken,
  isAuthenticated,
  getCurrentUser,
  validateSession,
  handleAuthError,
  withAuthRetry,
  getUserPermissions,
  isAdmin,
  getUserInfo,
  requireAuth
};