const OnlinePresence = () => {
  const [activePlatform, setActivePlatform] = useState('linkedin');
  
  const currentPlatform = platforms[activePlatform];

  return (
    <SectionWrapper>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           Building Your Online Presence
        </Title>
      </Header>

      <PlatformTabs>
        {Object.entries(platforms).map(([key, platform]) => (
          <PlatformTab
            key={key}
            active={activePlatform === key}
            color={platform.color}
            onClick={() => setActivePlatform(key)}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <span className="icon">{platform.icon}</span>
            <span>{platform.name}</span>
          </PlatformTab>
        ))}
      </PlatformTabs>

      <AnimatePresence mode="wait">
        <PlatformContent
          key={activePlatform}
          color={currentPlatform.color}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -20 }}
          transition={{ duration: 0.5 }}
        >
          <ContentSection color={currentPlatform.color}>
            <div className="section-title">
              <span className="section-icon"></span>
              {currentPlatform.name} Optimization Checklist
            </div>
            
            <ChecklistGrid>
              {currentPlatform.checklist.map((item, index) => (
                <ChecklistItem
                  key={index}
                  color={currentPlatform.color}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.1 }}
                >
                  <div className="item-header">
                    <span className="item-icon">{item.icon}</span>
                    <span className="item-title">{item.title}</span>
                  </div>
                  <div className="item-description">{item.description}</div>
                </ChecklistItem>
              ))}
            </ChecklistGrid>
          </ContentSection>

          <ExampleSection color={currentPlatform.color}>
            <div className="example-header">
              <span className="icon"></span>
              {currentPlatform.example.title}
            </div>
            <div 
              className="example-content"
              dangerouslySetInnerHTML={{ __html: currentPlatform.example.content }}
            />
          </ExampleSection>

          <SuccessStory 
            color={currentPlatform.color}
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5, delay: 0.3 }}
          >
            <div className="story-header">
              <div className="story-icon">{currentPlatform.successStory.icon}</div>
              <div className="story-title">{currentPlatform.successStory.title}</div>
            </div>
            <div className="story-content">{currentPlatform.successStory.content}</div>
          </SuccessStory>
        </PlatformContent>
      </AnimatePresence>

      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true }}
        style={{
          background: 'rgba(0, 255, 231, 0.1)',
          border: '1px solid rgba(0, 255, 231, 0.3)',
          borderRadius: '12px',
          padding: '2rem',
          textAlign: 'center',
          marginTop: '3rem'
        }}
      >
        <h3 style={{ color: '#00ffe7', marginBottom: '1rem', fontSize: '1.5rem' }}>
           Consistency is Key
        </h3>
        <p style={{ 
          color: '#e0e0e0', 
          fontSize: '1.1rem', 
          lineHeight: '1.6',
          marginBottom: '1rem'
        }}>
          Ensure consistency across all platforms. If your CV claims you know X, your GitHub or portfolio should have evidence of it.
        </p>
        <div style={{ 
          background: 'rgba(239, 68, 68, 0.2)', 
          border: '1px solid rgba(239, 68, 68, 0.4)',
          borderRadius: '8px',
          padding: '1rem',
          marginTop: '1rem'
        }}>
          <strong style={{ color: '#ef4444' }}> Warning:</strong>
          <span style={{ color: '#e0e0e0', marginLeft: '0.5rem' }}>
            Recruiters WILL check your online presence. Make sure everything aligns with your CV claims.
          </span>
        </div>
      </motion.div>
    </SectionWrapper>
  );
};

export default OnlinePresence;