import { useEffect, useCallback } from 'react';

export const useKeyboardNavigation = (options) => {
  const {
    currentSection,
    totalSections,
    onNavigate,
    isModalOpen,
    onModalClose
  } = options;

  const handleKeyPress = useCallback((e) => {
    // Don't handle keys if a modal is open (except Escape)
    if (isModalOpen && e.key !== 'Escape') return;

    switch (e.key) {
      case 'ArrowRight':
      case 'ArrowDown':
        if (currentSection < totalSections - 1) {
          onNavigate(currentSection + 1);
        }
        break;
      
      case 'ArrowLeft':
      case 'ArrowUp':
        if (currentSection > 0) {
          onNavigate(currentSection - 1);
        }
        break;
      
      case 'Home':
        onNavigate(0);
        break;
      
      case 'End':
        onNavigate(totalSections - 1);
        break;
      
      case 'Escape':
        if (isModalOpen && onModalClose) {
          onModalClose();
        }
        break;
      
      default:
        break;
    }
  }, [currentSection, totalSections, onNavigate, isModalOpen, onModalClose]);

  useEffect(() => {
    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [handleKeyPress]);
};